"use client"

interface ProgressBarProps {
  currentSection: number
  totalSections: number
}

export function ProgressBar({ currentSection, totalSections }: ProgressBarProps) {
  const progress = (currentSection / totalSections) * 100

  return (
    <div className="w-full bg-gray-100 dark:bg-gray-800 rounded-full h-2.5 mb-6">
      <div
        className="bg-gray-900 dark:bg-gray-200 h-2.5 rounded-full transition-all duration-300 ease-in-out"
        style={{ width: `${progress}%` }}
      />
      <div className="flex justify-between mt-2 text-sm text-gray-600 dark:text-gray-400">
        <span>Progress</span>
        <span>{Math.round(progress)}%</span>
      </div>
    </div>
  )
}
