"use client"

import { useState, useRef } from "react"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Button } from "@/components/ui/button"
import { ProgressBar } from "@/components/teacch/progress-bar"
import { ContentSection, sections } from "@/components/teacch/content"

export default function Home() {
  const [activeSection, setActiveSection] = useState(1)
  const [showContent, setShowContent] = useState(false)
  const contentRef = useRef<HTMLDivElement>(null)

  const scrollToContent = () => {
    setShowContent(true)
    setTimeout(() => {
      contentRef.current?.scrollIntoView({ behavior: 'smooth' })
    }, 100)
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-purple-50 dark:from-gray-900 dark:to-blue-900">
      {/* Hero Section */}
      <section className="relative min-h-screen flex flex-col items-center justify-center text-center px-6">
        {/* Animated Background */}
        <div className="absolute inset-0 overflow-hidden">
          <div 
            className="absolute inset-0 bg-gradient-to-br from-blue-600 via-purple-600 to-pink-500 opacity-90"
            style={{
              backgroundSize: '400% 400%',
              animation: 'gradient 15s ease infinite',
            }}
          />
          <div className="absolute inset-0 bg-[url('/grid.svg')] opacity-10" />
          {/* Floating Elements */}
          <div className="absolute inset-0">
            {[...Array(3)].map((_, i) => (
              <div
                key={i}
                className="absolute rounded-full mix-blend-multiply filter blur-xl opacity-70 animate-blob"
                style={{
                  backgroundColor: i === 0 ? '#60A5FA' : i === 1 ? '#C084FC' : '#F472B6',
                  width: '30rem',
                  height: '30rem',
                  left: i === 0 ? '20%' : i === 1 ? '50%' : '70%',
                  top: i === 0 ? '20%' : i === 1 ? '50%' : '30%',
                  animation: `blob-spin ${15 + i * 2}s linear infinite`,
                  animationDelay: `${i * -3}s`
                }}
              />
            ))}
          </div>
        </div>

        {/* Content */}
        <div className="relative z-10 max-w-5xl mx-auto text-white">
          <div className="space-y-6 mb-12">
            <h2 className="text-blue-200 text-xl font-medium animate-fade-in-up opacity-0" style={{ animationDelay: '0.2s' }}>
              Selamat Datang di
            </h2>
            <h1 className="text-6xl sm:text-7xl font-extrabold tracking-tight mb-6 animate-fade-in-up opacity-0" style={{ animationDelay: '0.4s' }}>
              TEACCH Toilet Training
            </h1>
            <p className="text-xl sm:text-2xl max-w-3xl mx-auto text-blue-100 animate-fade-in-up opacity-0" style={{ animationDelay: '0.6s' }}>
              Panduan interaktif toilet training bagi orang tua anak dengan gangguan spektrum autisme
            </p>
          </div>
          <div className="flex flex-col sm:flex-row items-center justify-center gap-4 sm:gap-6 animate-fade-in-up opacity-0" style={{ animationDelay: '0.8s' }}>
            <button
              onClick={scrollToContent}
              className="w-64 sm:w-auto px-8 py-4 text-lg font-semibold border-2 border-white text-white hover:bg-white hover:text-blue-600 transition-all duration-300 rounded-md"
            >
              Mulai Sekarang
            </button>
            <button
              onClick={() => {
                scrollToContent()
                setActiveSection(2)
              }}
              className="w-64 sm:w-auto px-8 py-4 text-lg font-semibold bg-white text-blue-600 hover:bg-blue-50 transition-all duration-300 rounded-md"
            >
              Pelajari Lebih Lanjut
            </button>
          </div>
        </div>

        {/* Scroll Indicator */}
        <button
          onClick={scrollToContent}
          className="absolute bottom-10 left-1/2 transform -translate-x-1/2 animate-fade-in-up opacity-0 text-white hover:text-blue-200 transition-colors flex flex-col items-center gap-2 cursor-pointer"
          style={{ animationDelay: '1s' }}
        >
          <span className="text-lg">Scroll untuk melihat panduan</span>
          <span className="animate-bounce text-2xl">↓</span>
        </button>
      </section>

      {/* Content Section */}
      <div 
        ref={contentRef}
        className={`transition-all duration-500 ${showContent ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}
      >
        <div className="container mx-auto px-4 py-16">
          <ProgressBar currentSection={activeSection} totalSections={sections.length} />
          
          <Tabs defaultValue="1" className="w-full mt-8">
            <TabsList className="grid w-full grid-cols-2 lg:grid-cols-5 mb-8 bg-white/50 dark:bg-gray-800/50 backdrop-blur-sm rounded-lg p-1 shadow-lg">
              {sections.map((section) => (
                <TabsTrigger
                  key={section.id}
                  value={section.id.toString()}
                  onClick={() => setActiveSection(section.id)}
                  className="data-[state=active]:bg-gradient-to-r data-[state=active]:from-blue-600 data-[state=active]:to-purple-600 data-[state=active]:text-white transition-all duration-300 hover:bg-white/80 dark:hover:bg-gray-700/80"
                >
                  {section.title}
                </TabsTrigger>
              ))}
            </TabsList>

            <ContentSection
              title={sections[activeSection - 1].title}
              description={sections[activeSection - 1].description}
              content={sections[activeSection - 1].content}
            />

            <div className="flex justify-between mt-8">
              <Button
                variant="outline"
                onClick={() => setActiveSection(Math.max(1, activeSection - 1))}
                disabled={activeSection === 1}
                className="w-32 bg-white hover:bg-gradient-to-r hover:from-blue-600 hover:to-purple-600 hover:text-white transition-all duration-300 shadow-sm"
              >
                Sebelumnya
              </Button>
              <Button
                onClick={() => setActiveSection(Math.min(sections.length, activeSection + 1))}
                disabled={activeSection === sections.length}
                className="w-32 bg-gradient-to-r from-blue-600 to-purple-600 text-white hover:from-blue-700 hover:to-purple-700 transition-all duration-300 shadow-sm"
              >
                Selanjutnya
              </Button>
            </div>
          </Tabs>
        </div>
      </div>

      <style jsx global>{`
        @keyframes gradient {
          0% { background-position: 0% 50%; }
          50% { background-position: 100% 50%; }
          100% { background-position: 0% 50%; }
        }
        
        @keyframes fade-in-up {
          from {
            opacity: 0;
            transform: translateY(20px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        
        @keyframes blob-spin {
          from { transform: rotate(0deg) translate(100px) rotate(0deg); }
          to { transform: rotate(360deg) translate(100px) rotate(-360deg); }
        }
        
        .animate-fade-in-up {
          animation: fade-in-up 0.6s ease-out forwards;
        }

        .animate-blob {
          animation: blob-spin 20s linear infinite;
        }

        html {
          scroll-behavior: smooth;
        }
      `}</style>
    </div>
  )
}
